@include tablet {
    .author {
        overflow: hidden;

        &__background {clip-path: polygon(0 14%, 100% 0, 100% 82%, 0% 100%);}

        // Position companies on center
        &__companies {
            align-items: baseline;
            position: relative;

            .item {
                position: absolute;

                padding-top: 32px;
                top: 0;

                width: 100%;
                height: 100%;

                opacity: 0;
                transition: all .5s ease;
                pointer-events: none;
            }

            // Slider
            .active-slide {
                opacity: 1;
                transform: translateX(0);
            }
    
            .next-slide {transform: translateX(100%);}
            
            .last-slide {transform: translateX(-100%);}
        }

        // Arrows
        .left-arrow__button,
        .right-arrow__button {
            background-color: transparent;
            border: 0;
            margin-top: -10px;
            width: 40px;
            height: 40px;
        }

        .companies__arrows {
            position: absolute;
            display: flex;
            justify-content: space-between;
            align-items: center;

            width: 100%;
            max-width: 300px;

            margin: 0 auto;
            padding-top: 32px;

            left: calc(50% - 150px);
            right: calc(50% - 150px);
            top: -25px;

            .arrow {
                height: 15px;
                z-index: 2;
            }
        }

        // Dots
        .companies__dots {
            display: flex;
            align-items: center;
            justify-content: space-between;

            width: 130px;
            margin: 20px auto 0;
            padding-top: 32px;

            .dot {
                background-color: #9e9e9e;

                width: 15px;
                height: 15px;
                
                border-radius: 50%;
                transition: all .5s ease;

                &.active {background-color: #696969;}
            }
        }
    }
}

@include mobileL {
    .author {
        &__background {
            clip-path: polygon(0 10%, 100% 0, 100% 89%, 0% 100%);
        }
    }
}
